let MauBinhEvent = {};
MauBinhEvent.RESPONSE_NAME = {
    SIT_ON_RES : "mb1",
    SIT_OUT_RES : "mb2",
    STAND_UP_RES : "mb3",
    PRE_START_RES : "mb4",
    START_RES : "mb5",
    KICK_RES : "mb6",
    LOAD_TABLE_RES : "mb7",
    SHOW_DOWN_RES: "mb8",
    FINISH_GAME_RES : "mb9",
    XEP_XONG_RES: "mb10",
    BAO_BINH_RES: "mb11",
};
MauBinhEvent.UserSitOnEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(MauBinhEvent.RESPONSE_NAME.SIT_ON_RES);
    },
    fromEvent(event){
        let param = event.get(MauBinhEvent.RESPONSE_NAME.SIT_ON_RES);
        this.m_deskId = param.getInt("did");
        this.m_userName = param.getUtfString("u");
        this.m_currentUser = param.getUtfString("cu");
        this.m_deskState = param.getUtfString("ds");
        this.m_buyIn = param.getDouble("c");
        return this;
    }
});
MauBinhEvent.UserSitOutEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(MauBinhEvent.RESPONSE_NAME.SIT_OUT_RES);
    },
    fromEvent(event){
        let param = event.get(MauBinhEvent.RESPONSE_NAME.SIT_OUT_RES);
        this.m_username = param.getUtfString("u");
        this.m_isSitOut = param.getBool("io");
        return this;
    }
});
MauBinhEvent.UserStandUp = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(MauBinhEvent.RESPONSE_NAME.STAND_UP_RES);
    },
    fromEvent(event){
        let param = event.get(MauBinhEvent.RESPONSE_NAME.STAND_UP_RES);
        this.m_username = param.getUtfString("u");
        return this;
    }
});
MauBinhEvent.PreStartEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(MauBinhEvent.RESPONSE_NAME.PRE_START_RES);
    },
    fromEvent(event){
        this.m_isPrestart   = event.getBool("ip");
        this.m_prestartTime = event.getInt("t");
        return this;
    }
});
MauBinhEvent.StartEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(MauBinhEvent.RESPONSE_NAME.START_RES);
    },
    fromEvent(event){
        this.m_gameTime = event.getInt("t")/1000;
        this.m_listCard = event.getByteArray("lc");
        this.m_listActiveUserName = event.getUtfStringArray("lu");
        return this;
    }
});
MauBinhEvent.KickUserEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(MauBinhEvent.RESPONSE_NAME.KICK_RES);
    },
    fromEvent(event){
        let param = event.get(MauBinhEvent.RESPONSE_NAME.KICK_RES);
        this.m_resType = param.getInt("rt");
        this.m_username = param.getUtfString("u");
        return this;
    }
});
MauBinhEvent.FinishGameEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(MauBinhEvent.RESPONSE_NAME.FINISH_GAME_RES);
        this.players = [];
    },
    fromEvent(event){
        let arrPlayer = event.getSArray("d");
        for(let i = 0; i < arrPlayer.size();i++){
            let sPlayer = arrPlayer.get(i).getObject();
            let player = {};
            player.username = sPlayer.getUtfString("un");
            player.typeBinh = sPlayer.getByte("tb");
            player.moneyWin = sPlayer.getByteArray("w");
            player.winsapham = sPlayer.getBool("s");
            player.cards     = sPlayer.getByteArray("lc");
            this.players.push(player);
        }
        return this.players;
    }
});
MauBinhEvent.LoadTableInfoEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(MauBinhEvent.RESPONSE_NAME.LOAD_TABLE_RES);
        this.m_isGameStart = false;
        this.m_isPrestart = false;
        this.m_prestartTime = 0;
        this.m_listDesk = [];
        this.m_listUserSitOut = [];
        this.m_listUser = [];
        this.m_listActiveUserName = [];
        this.m_listHandCard = [];
    },
    fromEvent(event){
        let param = event.get(MauBinhEvent.RESPONSE_NAME.LOAD_TABLE_RES);
        this.m_isGameStart = param.getBool("is");
        this.m_isPrestart = param.getBool("ip");
        this.m_prestartTime = param.getInt("pt");

        let sfsListDesk = param.getSArray("ld");
        for(let i = 0; i < sfsListDesk.size(); i++){
            let desk = this.deskFromSObject(sfsListDesk.get(i).getObject());
            this.m_listDesk.push(desk);
        }

        this.m_listUserSitOut = param.getUtfStringArray("luo");
        this.m_listUser = param.getUtfStringArray("lu");
        this.m_listActiveUserName = param.getUtfStringArray("lau");

        let sfsListHandCard = param.getSArray("lc");
        for(let i = 0; i < sfsListHandCard.size();i++){
            let sfsHandCards = sfsListHandCard.get(i).getObject();
            let cards = [];
            for(let j = 0; j < sfsHandCards.size();j++){
                cards.push(sfsHandCards.get(j).getObject());
            }
            this.m_listHandCard[i] = cards;
        }
        return this;
    },
    deskFromSObject(obj){
        let desk = {};
        desk.DeskId = obj.getInt("id");
        desk.UserName = obj.getUtfString("u");
        desk.DeskState = obj.getUtfString("s");
        desk.UserChip = obj.getDouble("c");
        return desk;
    }
});
MauBinhEvent.ShowDownEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(MauBinhEvent.RESPONSE_NAME.SHOW_DOWN_RES);
        this.players = [];
    },
    fromEvent(event){
        let arrPlayer = event.getSArray("d");
        for(let i = 0; i < arrPlayer.size();i++){
            let sPlayer = arrPlayer.get(i).getObject();
            let player = {};
            player.username = sPlayer.getUtfString("un");
            player.typeBinh = sPlayer.getByte("tb");
            player.typeChi = sPlayer.getByteArray("tc");
            player.moneyChi = sPlayer.getDouble("mc");
            player.cards = sPlayer.getByteArray("lc");
            this.players.push(player);
        }
        return this.players;
    }
})

MauBinhEvent.XepXongResEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(MauBinhEvent.RESPONSE_NAME.XEP_XONG_RES);
        this.error = "";
    },
    fromEvent(event){
        this.error = event.containsKey("ec") ? event.getUtfString("ec") : "";
        this.m_username = event.containsKey("u") ? event.getUtfString("u") : "";
        return {error: this.error, username: this.m_username};
    }
})

MauBinhEvent.BaoBinhResEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(MauBinhEvent.RESPONSE_NAME.BAO_BINH_RES);
        this.error = "";
    },
    fromEvent(event){
        this.error = event.containsKey("ec") ? event.getUtfString("ec") : "";
        this.m_username = event.containsKey("u") ? event.getUtfString("u") : "";
        this.typeBinh = event.containsKey("tb") ? event.getByte("tb") : "0";
        return {error: this.error, username: this.m_username, typeBinh: this.typeBinh};
    }
})
window.MauBinhEvent = module.exports = MauBinhEvent;
